//greyscale colors
$gsbg : #D8D8D8;
$gsnavbar : #B8B4B4;
$gstopbar : #B5B2B2;
$gsnavactive : #F4F4F4;
$gscontentbg : #F4F4F4;
$gscontentitem: #EBE6E6;
$gscontentbutton : #C4C4C4;
$gscontentNavbar : #E5E5E5;

$textsizeL: 18px;
$textsizeM: 16px;
$textsizeS: 13px;

html { margin-left: calc(100vw - 100%); }
body{
    background-color:$gsbg;
    font-family:'Roboto',sans-serif;
    font-size: $textsizeM;
}

i{
    font-style:italic;
}

h1,h2,p,label{
    padding:1rem;
}
h1{
    font-size:3rem;
}
h2{
    font-size:2rem;
}
a, a:visited, a:hover, a:active {
    color: inherit;
    text-decoration:none;
}
input, textarea,p,label{
    font-size:inherit;
}

.main{
    display:flex;
    flex-direction:column;
    justify-content:center;
    flex-wrap:wrap;
    background-color: $gstopbar;
}

.topBar{
    display:flex;
    background-color:$gstopbar;
    height:60px;
    justify-content:center;
    .logo-image{
        padding:10px;
        min-width:60px;
        min-height:60px;
    }
}

.navBar{
    background-color:$gsnavbar;
    display:flex;
    flex-direction:column;
    justify-content:center;
    text-align:center;
}


.navLinks{
    padding: 1rem 0;
    display:flex;
    flex-direction: column;            
    border:1px;
        .navLink {
            font-size:1.5rem;
            padding:1.5rem 0rem;
    transition-duration:0.4s;
    }
    .navLink:hover{
        background-color:$gscontentbg;
    }

}



.buttn{
    border:none;
    padding:15px 32px;
    font-size: $textsizeL;
    margin-bottom:20px;
    background-color:$gscontentbutton;
    transition-duration:0.4s;
}

.buttn-success{
    border:none;
    padding:15px 32px;
    font-size: $textsizeL;
    margin-bottom:20px;
    background-color:green;
    color:white;
    transition-duration:0.4s;

}

.buttn:active,.buttn:hover,.links:hover,.links:active{
    background-color: $gscontentitem;
}

.avatar{
        display:flex;
        justify-content:center;
        flex-direction:column;
    .avatar-image{
        align-self:center;
        display:block;
        text-align:center;
        min-width:2rem;
        min-height:2rem;
        max-width:12rem;
        max-height:12rem;
        img{
        border-radius: 30px;
        overflow:hidden;
        height:100%;
        width:auto;
        }
    }
}

.links{
    align-self:flex-end;
    padding:0.5rem;
    margin:0.5rem;
    transition-duration:0.4s;
    background-color:$gscontentbutton;
}

.is-active{
    background-color: $gscontentbg;
}

// Content
.statusBarParent{
    background-color:$gstopbar;
}
.statusBar{
    height:32px;
    background-color: $gscontentNavbar;
    display:flex;
    justify-content: flex-end;
    .lang{
        border:none;
        background-color:$gscontentNavbar;
    }
.is-active{
    background-color: $gscontentbg;
}
    .lang:hover{
        background-color: $gscontentbg;
    }
}

#content{
    background-color: $gscontentbg;
}

.content-item{
    background-color: $gscontentNavbar;
    margin:1rem;
    display:flex;
    flex-direction:column;
}


// Home 
.home{
    display:flex;
    flex-direction:column;
    flex-wrap:wrap;
    .welcome{
        a{
            align-self:center;
            max-width:15rem;
        }
            text-align: center;
        h1{
            padding:2rem;
        }
        p{
            font-size:$textsizeL;
        }
 
    }
    .latest-blog{
        flex: 0 0 50%;
    }

    .fluff{
        flex: 1 0 0;
        min-width:4rem;
        min-height:10rem;
    }

}
// About
.about{
    flex-direction:row;
    flex-wrap: wrap;
    justify-content:center;
    .about-picture{
        max-height:30rem;
        max-width:20rem;
        display:block;
        margin:1rem;
        // justify-content:center;
        img{
            text-align:center;
           height:100%;
           width:auto;
        }
    }

    .about-facts{
        margin:1rem;
        padding:1rem;
        background-color: $gscontentbutton;
    }

    .about-contact{
        margin:1rem;
        display:flex;
        justify-content:center;
        flex-direction: column;
        text-align:center;
        a{
            align-self:center;
        }
        p{
        padding:0rem;
        }
    }
}


// Blog
.blogPost{
    p{
        line-height:24px;
    }
}
// Contact
.contact-form{
    padding:1rem;
    display:flex;
    flex-direction:column;
    button{
        max-width:10rem;
        margin: 1rem 1rem;
    }

}

.contact-input{
    max-width:15rem;
    background-color:$gscontentbg;
    margin:0 1rem;
    padding:1rem;
}
#message{
    resize:vertical;
    min-height:5rem;
    background-color: $gscontentbg;
    margin:0 1rem;
    padding:1rem;
}
.error{
    // padding:12px;
    display:table;
    background-color:lightblue;
    margin:0.5rem 1rem;
    padding:1rem;
}


// Media queries

@media (min-width: 640px){
    .main{
        flex-direction:row;
        max-width:1200px;
        margin:auto;
        margin-top:30px;
        margin-bottom:30px;

        -webkit-box-shadow: 10px 10px 5px -8px rgba(0,0,0,0.75);
        -moz-box-shadow: 10px 10px 5px -8px rgba(0,0,0,0.75);
        box-shadow: 10px 10px 5px -8px rgba(0,0,0,0.75);
        border-radius:5px;
        overflow:hidden;
    }
    .content-item{
        -webkit-box-shadow: 3px 5px 16px -10px rgba(0, 0, 0, 0.75);
-moz-box-shadow: 3px 5px 16px -10px rgba(0, 0, 0, 0.75);
box-shadow: 3px 5px 16px -10px rgba(0, 0, 0, 0.75);
    }

 


    .navLinks{
        padding-left:10px;
    }
    .navLink{
        border-radius:50px 0px 0px 50px;
    }
    .is-active{
        -webkit-box-shadow: 5px 9px 5px -7px rgba(0,0,0,0.75);
-moz-box-shadow: 5px 9px 5px -7px rgba(0,0,0,0.75);
box-shadow: 5px 9px 5px -7px rgba(0,0,0,0.75);
    }

    content{
    border-radius: 0 0 0 20px;
    }
    .contact-input , #message , .buttn , .buttn-success, .buttn:active, .buttn:hover, .content-item, .about-facts, .links{
        border-radius:10px;
    }

.contact-input-company{
    max-width:15rem;
    margin:0 1rem;
    padding:1rem;
    background-color:$gscontentbg;
    transition-delay: 0.3 background-color ease;
}
#companyRepresented[value="true"]  .contact-input-company {
    background-color:red;
  } 
    .statusBar{
        border-radius:20px 20px 0 0;
        overflow:hidden;
    }
   
    #content{
        flex: 1 0 0;
    }
    .navBar{
        flex:0 0  15%;
        padding-top:1rem;
        min-height: 80vh ;
        justify-content:flex-start;
        .colorTheme{
            margin-top: auto;
        }
        
    }
    .topBar{
        flex:0 0 100%;
        justify-content:flex-start;
    }

    .home{
        flex-direction:row;


    }
    .welcome{
        flex: 0 0 97%;
    }

    .about-intro{
        flex:0 0 50%;
    }
    .about-picture{
        flex:1 0 0;
    }
    .about-history{
        flex:0 0 75%;
    }
    .about-facts{
        flex:1 0 0;
    }


        
}
        